@import "./src/assets/scss/_mixins.scss";
              @import "./src/assets/scss/_variables.scss";

.form {
  &__label {
    @include flexible();
    flex-direction: column;
    box-sizing: border-box;
    border: 1px solid $colors-gray-l;
    border-radius: 4px;
    padding: 8px 16px;
    color: $colors-gray;
    font-size: 13px;
  }

  &__input {
    padding: 0;
    width: 100%;
    border: none;
    line-height: 1.71;
    box-sizing: border-box;
    &::placeholder {
      color: red;
    }

    &:focus {
      outline: none;
      border-bottom: 1px solid $colors-gray;
    }

  }
}

