@import "./src/assets/scss/_mixins.scss";
              @import "./src/assets/scss/_variables.scss";

.main {
  @include gridable();
  box-sizing: border-box;
  grid-row: 2;

  &__container {
    @include flexible();
    flex-direction: column;
    align-items: center;
    padding: 20px;
    box-sizing: border-box;
  }
}

.fade {
  &-enter-active, &-leave-active {
    transition: opacity 0.5s linear;
  }

  &-enter, &-leave-to {
    opacity: 0;
  }

}
