@import "./src/assets/scss/_mixins.scss";
              @import "./src/assets/scss/_variables.scss";

.footer {
  @include flexible();
  justify-content: center;
  box-sizing: border-box;
  padding: 30px 92px;
  background-color: $colors-gray-m;
  box-shadow: $box-shadow;

  &__text {
    text-transform: lowercase;
    font-size: 13px;
  }
}
