@import "./src/assets/scss/_mixins.scss";
              @import "./src/assets/scss/_variables.scss";

.form {
  &__fieldset {
    @include flexible();
    flex-direction: column;
    border: none;
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    gap: 10px 18px;

    &_row {
      flex-direction: row;
      flex-grow: 0;
      flex-wrap: wrap;
      align-items: center;
    }

    &-title {
      @include defaultTitle;
    }
  }
}
